import React, { useEffect, useState } from "react";
import { useInterval } from "react-interval-hook";

function Portfolio(props) {
  const [images, setImages] = useState([]);
  const [imgFilter, setImgFilter] = useState(null); //r,c,i
  const [imgIndex, setImgIndex] = useState(0);
  const [imgReady, setImageReady] = useState(false);
  const [imgsToShow, setImgsToShow] = useState([images.length - 1, 0, 1]);

  const [filteredImages, setFilteredImages] = useState([]);

  async function fetchUrl(url) {
    const response = await fetch(url);
    const images = await response.json();
    setImages([...images]);
  }

  const { start, stop } = useInterval(() => {
    moveNext();
  }, 8000);

  useEffect(() => {
    document.title = "Draftbox Design / Porfolio";
    fetchUrl("index.json");
  }, []);

  useEffect(() => {
    setFilteredImages(
      images?.filter((i) => !imgFilter || i.type === imgFilter) || []
    );
  }, [imgFilter, images]);

  useEffect(() => {
    setImgIndex(0);
  }, [filteredImages]);

  useEffect(() => {
    setImgsToShow([
      imgIndex === 0 ? filteredImages.length - 1 : imgIndex - 1,
      imgIndex,
      imgIndex === filteredImages.length - 1 ? 0 : imgIndex + 1,
    ]);
  }, [imgIndex, filteredImages]);

  function moveNext() {
    let newIdx = imgIndex + 1;
    if (newIdx === filteredImages.length) {
      newIdx = 0;
    }

    setImageReady(false);
    setImgIndex(newIdx);
  }

  function movePrev() {
    let newIdx = imgIndex - 1;
    if (newIdx === -1) {
      newIdx = filteredImages.length - 1;
    }
    setImageReady(false);
    setImgIndex(newIdx);
  }

  function imageReady() {
    setImageReady(true);
  }

  function selectFilter(filter) {
    setImgFilter(filter);
  }

  return (
    <div className="portfolio">
      <h3 style={{ zIndex: 1 }}>
        <img
          src="../images/minibox.png"
          alt=""
          style={{ height: "25px", marginRight: "5px" }}
        />
        &nbsp;Portfolio
      </h3>

      <div className="projectCategories">
        <div
          onClick={() => selectFilter(null)}
          className={!imgFilter ? "active" : ""}
        >
          <i className="fa fa-th-list"></i>
        </div>
        <div
          onClick={() => selectFilter("r")}
          className={imgFilter === "r" ? "active" : ""}
        >
          <i className="fa fa-home"></i>
        </div>
        <div
          onClick={() => selectFilter("c")}
          className={imgFilter === "c" ? "active" : ""}
        >
          <i className="fa fa-building-o"></i>
        </div>
        <div
          onClick={() => selectFilter("i")}
          className={imgFilter === "i" ? "active" : ""}
        >
          <i className="fa fa-industry"></i>
        </div>
      </div>

      <div className={"imgPreview " + (imgReady ? "imgReady" : "")}>
        <div style={{ zIndex: 1 }} className="imgNav">
          <div
            onClick={() => {
              stop(false);
              movePrev();
              start();
            }}
          >
            <i className="fa fa-chevron-left"></i>
          </div>
          <div className={imgReady ? "imgReady" : ""}>
            {imgReady ? "" : "loading..."}
          </div>
          <div
            onClick={() => {
              stop(false);
              moveNext();
              start();
            }}
          >
            <i className="fa fa-chevron-right"></i>
          </div>
        </div>

        {/* <div className="imagePlay">
          <i className="fa fa-pause"></i>
        </div> */}

        <div className="imgsToShow">
          {imgsToShow.map((i) => {
            if (!filteredImages[i]) {
              return "";
            }
            return (
              <div
                key={i}
                style={{
                  backgroundImage: `url(../portfolio/${filteredImages[i].filename})`,
                }}
              >
                {/* <section></section> */}
              </div>
            );
          })}
        </div>
        {filteredImages[imgIndex] && (
          <>
            <img
              style={{ height: "10px", opacity: 0 }}
              alt={`${filteredImages[imgIndex].filename}`}
              src={`../portfolio/${filteredImages[imgIndex].filename}`}
              onLoad={() => imageReady()}
            ></img>
          </>
        )}
      </div>
    </div>
  );
}

export default Portfolio;
